style="float: right;"*/
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,100,300,600,700);

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.dad {
  background-image: url("https://bufiles.blob.core.windows.net/co3613/vertical_menu/5.jpg");
  width: 100%;
  padding: 0; 
}
.son {
  float: left; }

.grow {
  text-decoration: none;
  padding: 5px 5px 5px 5px;
  height: 100vh;
  width: 25vw;
  float: left;
  position: relative;
  -o-transition: width 0.5s;
  transition: width 0.5s;
  -webkit-transition: width 0.5s;
  text-align: left;
  overflow: hidden; }

.grow:hover {
  height: 100vh;
  /*width: 26vw;*/
  text-align: left;
}


.tab-title {
  font-size: 2.5em;
  color: #fcfcfc;
  text-transform: uppercase;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400; }

.vertical-tab-1 {
  background: url("https://bufiles.blob.core.windows.net/co3613/vertical_menu/7.jpg") center center no-repeat; 
}

.vertical-tab-2 {
  background: url("https://bufiles.blob.core.windows.net/co3613/vertical_menu/8.jpg") center center; }

.vertical-tab-3 {
  background: url("https://bufiles.blob.core.windows.net/co3613/vertical_menu/4.jpg") center center; }

.vertical-tab-4 {
  background: url("https://bufiles.blob.core.windows.net/co3613/vertical_menu/9.jpg") center center; }

.rotate {
  -ms-transform: rotate(-90deg);
      transform: rotate(-90deg);
  -webkit-transform: rotate(-90deg);
  margin-top: 50vh;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
  color: #fff;
  font-size: 2.25em;
  text-transform: uppercase; }

@media screen and (max-width: 970px) {
  .rotate {
    font-size: 1.5em; } }
@media screen and (max-width: 970px) {
  .tab-title {
    font-size: 1.6em; } }
@media screen and (max-width: 767px) {
  .rotate {
    font-size: 1.125em; } }
@media screen and (max-width: 767px) {
  .tab-title {
    font-size: 1.25em; } }
@media screen and (max-width: 500px) {
  .rotate {
    font-size: 0.6em; } }
@media screen and (max-width: 500px) {
  .tab-title {
    font-size: 0.75em; } }


#about .rotateAbout {
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  margin-top: 50vh;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
  color: #fff;
  font-size: 2.25em;
  text-transform: uppercase; 
}


